import React from "react";
/**
* Componente consistente en enlaces con una direccin, id y contenido dinmico
* @param {string} props.direction - Direccin a la que nos llevar el botn
* @param {string} props.id - Id del botn, para darle estilos concretos
* @param {string} props.children - El texto que tendr el botn
* @returns {JSX} 
*      
*/
function Button ( props ){
    return(
    <a href={`${props.direction}.html`} id={`${props.id}`} className="buttons">{`${props.children}`}</a>
    )
}
export default Button;